import imgDonut from './img-donut.jpg';
import imgMyrtle from './img-myrtle-ave.jpg';
import imgSpicedRum from './img-spiced-rum.jpg';
import imgBedford from './img-bedford-bizarre.jpg';
import imgBerryblitz from './img-berryblitz.jpg';
export const TeaSection = () => {
  const styles = {
    width: "1000px",
  };

 

  const images = [
    { src: imgBerryblitz, text: "Fall Berry Blitz Tea" },
    { src: imgSpicedRum, text: "Spiced Rum Tea" },
    { src: imgDonut, text: "Seasonal Donuts" },
    { src: imgMyrtle, text: "Myrtal Ave Tea" },
    { src: imgBedford, text: "Bedford Bizarre Tea" },
  ];

  return (
    <section id='tea' className="tea-section" style={styles}>
      <div>
        <h2>Tea of the Month</h2>
        <p>What's Steeping at The Tea Cozy?</p>
      </div>

      {images.map((image) => {
        return (
          <a className="img-container" key={image.text}>
            <img src={image.src} alt={image.text} width="300" height="200" />
            <p>{image.text}</p>
          </a>
        );
      })}
    </section>
  );
};
